.projectsHolder {
    display: flex;
    width: 80vw;
    margin: auto;
    padding: 10px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
      rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  }
  
  .ImageHolder img {
    width: 100%;
    height: 100%;
  }
  
  .ImageHolder {
    /* border: 1px solid red; */
    width: 50%;
    height: auto;
    padding: 5px;
  }
  
  button {
    padding: 10px;
    margin-right: 5px;
  }
  
  .projectsDetais {
    /* border: 1px solid red; */
    height: auto;
    text-align: left;
    padding: 5px;
    width: 50%;
  }
  
  @media screen and (max-width: 600px) {
    .projectsHolder {
      /* background-color: green; */
      flex-direction: column;
    }
  
    .projectsDetais {
      height: 50%;
      width: 100%;
    }
  
    .ImageHolder {
      height: 50%;
      /* border: 1px solid red; */
      padding: 0px;
      width: 100%;
    }
  
    .ImageHolder img {
      width: 90%;
      height: 100%;
    }
  }
  