header{
    height: 100vh;
    padding-top: 2rem;
    /* overflow: hidden; */
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
    /* border: 1px solid yellow; */
}


/* ------CAT---------------- */

.cta{
    margin-top: 2.5rem;
    display:flex;
    gap: 1.2rem;
    justify-content: center;
}


/* ---Header Socials */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ------me --------*/

.me{
    color: var(--color-bg);
    /* color:white; */
    width: 80%;
    height: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    /* left: calc(50% -11rem); */
    margin-top: 4rem;
    border-radius: 0rem 12rem 10 0;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    /* overflow:hidden; */
    background-image: url('../../assets/react.gif');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    box-shadow: black;
    /* border:1px solid red; */
}



/* -------scroll down--------- */

.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width: 1024px){
    header{
        height:68vh;
    }
}

@media screen and (max-width: 600px){
    header{
        height:100vh;
    }
    .header__socials,.scroll__down{
        display: none;
    }
}





