nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    /* background: var(--color-light); */
    color: var(--color-white);
}

.navholder{
    position: relative;
}

.navholder:hover .NavDetails{
    display: flex;
}

.NavDetails{
    position: absolute; 
    bottom:130%;
    left: -45%;
    display: none;
    width:auto;
    height:auto;
    font-size: 15px;
    padding: 10px;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    background-color:rgba(0, 0, 0, 0.3);
    color: white;
}

.NavDetails::after{
    content:' ';
    z-index: 2;
    width:0px;
    height:0px;
    position: absolute;
    bottom: -30%;
    left: 40%;
    border-top: 10px solid rgba(0, 0, 0, 0.3);
    border-bottom: 10px solid transparent;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
}
