.about__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width:100%;
    height: 80vh;
    gap:2%;
    /* border:1px solid red; */
    border-radius: 10px;
    background-color: var(--color-primary-variant);
}

.about__me{
    width:100%;
    height: 60%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:10px;
    margin: auto;
    text-align: left;
    /* border:5px solid green; */
}

.intro{
    width: 70%;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    width: auto;
    height: 97%;
    /* object-fit: cover; */
    /* border-radius: 50%; */
    /* transform: rotate(-10deg); */
    transition: var(--transition);
}



.about__content{
    width:70%;
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5rem;
    margin: auto;
}

.about__card{
    background:var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icons{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.9rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}




@media screen and (max-width:500px){
    .about__me{
        flex-direction:column;
    }
    .about__me-image{
        width: 40%;
        height: 100%;
        border-radius: 50%;
        /* border: 1px solid red; */
    }
    .intro{
        width: 70%;
        font-size:10px;
        /* border: 1px solid yellow; */
    }

    .about__card{
        padding: 2px;
        text-align: center;
        transition: var(--transition);
    }

}






